import { Component, OnInit } from '@angular/core';
import * as help from "./help.json";

@Component({
  selector: 'udb-help',
  templateUrl: './help.component.html',
  styleUrls: ['./help.component.scss']
})
export class HelpComponent implements OnInit {

  snr;
  help=(<any>help).sections;

  constructor() { }

  addClickToQuestions(){
    Object.keys(this.help).map((key,element) => {
      this.help[key].map((element)=>{
        return element.open = false;
      });
    });
  }

  ngOnInit() {
    this.addClickToQuestions();
  }

}
